import $store from '../store/index.js';
export default {
    // 
    common:{

		baseUrl:"http://ceshi8.dishait.cn",




        header:{
            'Content-Type':'application/json;charset=UTF-8',
        },
        data:{},
        method:'GET',
        dataType:'json',
        token:false
    },
    //  promise
    request(options = {}){
        // 
        options.url = this.common.baseUrl + options.url
        options.header = options.header || this.common.header
        options.data = options.data || this.common.data
        options.method = options.method || this.common.method
        options.dataType = options.dataType || this.common.dataType
        options.token = options.token === true ?  true : this.common.token

        // 
        return new Promise((res,rej)=>{
			// ...
			// token
			if(options.token){
				let token = $store.state.token
				// headertoken
				options.header.token = token
				
				// 
				if(!token && options.noJump !== true){
					uni.showToast({ title: '', icon: 'none' });
					// token
					uni.navigateTo({
						url: '/pages/login/login',
					});
					return rej("") 
				}
			}
			
			// if (options.token) {
			//     let token = uni.getStorageSync('token')
			//     // 
			//     if (!token && options.noJump !== true) {
			//         uni.showToast({ title: '', icon: 'none' });
			//         // token
			// 		uni.navigateTo({
			// 		    url: '/pages/login/login',
			// 		});
			// 		return rej("") 
			//     }
			//     // headertoken
			//     options.header.token = token
			// }
			
            // ...
            uni.request({
                ...options,
                success: (result) => {
                    // 
                    if(options.native){
                        return res(result)
                    }
                    // 
                    if(result.statusCode !== 200){
                        if (options.toast !== false) {
                            uni.showToast({
                                title: result.data.data || '',
                                icon: 'none'
                            });
                        }
						// token
						if(result.data.data === 'Token !'){
							$store.dispatch('logout')
						}
                        return false
                    }
                    // ...
                    // 
                    let data = result.data.data
                    res(data)
                },
                fail: (error) => {
                    uni.showToast({ title: error.errMsg || '', icon: 'none' });
                    return rej(error)
                }
            });
        })
    },
    // get
    get(url,options = {}){
        options.url = url
        options.data = {}
        options.method = 'GET'
        return this.request(options)
    },
    // post
    post(url,data = {},options = {}){
        options.url = url
        options.data = data
        options.method = 'POST'
        return this.request(options)
    },
    // delete
    del(url,data = {},options = {}){
        options.url = url
        options.data = data
        options.method = 'DELETE'
        return this.request(options)
    },
	// 
	upload(url,data,onProgress = false){
		return new Promise((result,reject)=>{
			// 
			let token = uni.getStorageSync('token')
			if (!token) {
			    uni.showToast({ title: '', icon: 'none' });
			    // token
			    return uni.navigateTo({
			        url: '/pages/login/login',
			    });
			}
			
			const uploadTask = uni.uploadFile({
				url:this.common.baseUrl + url,
				filePath:data.filePath,
				name:data.name || "files",
				header:{ token },
				success: (res) => {
					if(res.statusCode !== 200){
						result(false)
						return uni.showToast({
							title: '',
							icon: 'none'
						});
					}
					let message = JSON.parse(res.data)
					result(message.data);
				},
				fail: (err) => {
					console.log(err);
					reject(err)
				}
			})
			
			uploadTask.onProgressUpdate((res) => {
				if(typeof onProgress === 'function'){
					onProgress(res.progress)
				}
			});
			
		})
	}
}